.container {
    width: 100vw;
    display: flex;
    align-items: flex-start;
    gap: 20px;
    flex-wrap: wrap;
    padding: 5px 10px;
    justify-content: space-evenly;
}

.dashboard-card {
    display: flex;
    align-content: center;
    flex-direction: column;
    gap: 5px;
    width : 300px;
    background-color: whitesmoke;
}

.row{
    height: 800px;
}

.column{
    float: left;
    width: 17%;
    padding: 5px;
}

.card-heading {
    padding: 15px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.image {
    margin-right: 5px;
    position: relative;
    display : inline-block;
    width: 15px; 
    height: 15px;  
}

img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.select-list {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 10px;
}